import React, { Component } from 'react';
import axios from 'axios';



export default class BahisList extends Component {
  constructor(props){
    super(props);
    var aaa = JSON.parse(localStorage.getItem('Authorization').accessToken);

    axios.defaults.headers.common['Authorization'] = aaa;
    console.log(aaa.accessToken);
    
    axios.get('http://localhost:5000/bahisler')
    .then(res => console.log(res))
    .catch(err => console.log(err));

  }
  render() {
    return (
      <div>
        <p>Bahis listeleri kısmındasın.</p>
      </div>

    );
  }
}